import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    children:[
      {
        path: '/home',
        name: 'SnMain',
        component: () => import('../components/SnMain.vue')
      },
      {
        path: '/about',
        name: 'SnAbout',
        component: () => import('../components/SnAbout.vue')
      },
      {
        path: '/download',
        name: 'SnDownload',
        component: () => import('../components/SnDownload.vue')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
